@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color:#333;
}
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  position: sticky;
  top: 0px;
}
.navbar h1{
  color: #f1356d;
}
.navbar .links{
  margin-left: auto;
}
.navbar a{
  margin: 16px;
  text-decoration: none;
  padding: 6px;
  cursor: pointer;
}

.navbar a:active{
  color: blue;
}


.coin-box{
    display: flex;
    justify-content: center;
}
.coin-row{
    display: inline-flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
    justify-content: space-around;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    margin-left: auto;
    margin-right: auto;
    width: 110px;
}
.coin-volume{
    width: 230px;
    margin-left: auto;
    margin-right: auto;
}
.coin-marketcap{
    width: 155px;
    margin-left: auto;
    margin-right: auto;
}
.coin-percent{
    width: 100px;
    margin-left: auto;
    margin-right: auto;
}

.red{
    color: #f00606;
}
.green{
    color: #11d811;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  position: sticky;
  top: 71px;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

* {
  margin: 0;
  font-family: "Quicksand";
}
.title{
  display: inline-flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 900px;
  justify-content: space-between;
  font-size: medium;
  }
.create-container{
  margin: 20px auto;
  text-align: center;
}
.title {
  font-size: 2em;
}
.instructions {
  width: 75%;
  margin: 30px auto;
}
.form {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  border-style: solid;
  border-radius: 10px;
  width: 50%;
  margin: 50px auto;
  padding-top: 3em;
  padding-bottom: 1em;
  overflow: auto;
  line-height: 1;
}
form select{
  margin:10px 30px;
}
.amount{
  text-align: right;
  margin: 10px 30px;
}
.base{
  margin: 50px 10px;
}
footer {
  position: fixed;
  width: 100%;
  right: 0;
  bottom: 0;
  text-align: end;
  padding-right: 20px;
}


/* media sizings */

/* cell phone */
@media only screen and (max-width: 599px) {

  .title {
    font-size: 1em;
  }

  .instructions {
    width: 75%;
  }
  .form {
    width: 75%;
  }
}

